import React,{useState,useEffect} from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import './myStyles.css'
import {Nav,NavDropdown,Navbar,Form,FormControl,Button,Modal} from 'react-bootstrap'
import styled from 'styled-components'
import logo from '../static/logoPoy.png'

const Butt=styled.button `
    color:green;
    font-size:20px;
    font-weight:bold;
    background-color:white;
    border-radius:8px;
`


function Navigationbar(props) {
    const [show, setShow] = useState(false);
    const [w,setW]=useState(window.screen.width);
    const x=window.screen.width;
    console.log(x);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    function logoHandler(){
        setW(window.innerWidth);
        console.log(w);
     }

    
    

    window.onresize=logoHandler;
    
    return (
        <div className="container-fluid" id="navi">
            <Navbar bg="dark" expand="lg">
            <Navbar.Brand href="#home" style={{color:"white"}}> Home</Navbar.Brand>
            {(()=>{if(w<991)return(<img id="logo" src={logo}/>)})()}
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            
            

            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">
                <Nav.Link href="#home" onClick={handleShow} style={{color:"white"}}>Login</Nav.Link>
                <Nav.Link href="#link" style={{color:"white"}}>Link</Nav.Link>
                <NavDropdown title="Dropdown" id="basic-nav-dropdown" >
                    <NavDropdown.Item href="#opWin"  >Topslip</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.2">Aliyar</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.3">valparai</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action/3.4">Maps</NavDropdown.Item>
                </NavDropdown>
                {(()=>{if(w>767)return( <img id="logo" src={logo} /> )})()}
                </Nav>
                <Form inline>
                <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                <Butt variant="outline-success">Search</Butt>
                </Form>
            </Navbar.Collapse>
            </Navbar>

           

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                <Modal.Title>Hello User</Modal.Title>
                </Modal.Header>
                <Modal.Body>Welcome to Pollachi!</Modal.Body>
                <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                    Close
                </Button>
                <Button variant="primary" onClick={handleClose}>
                    Save Changes
                </Button>
                </Modal.Footer>
            </Modal>
      
  
        </div>
    )
}

export default Navigationbar
