import React from 'react'
import {Card,Button} from 'react-bootstrap'
import Aa from '../static/Aazhiyar1.jpg'
import Tops from '../static/TopSLip3.jpg'
import Bal from '../static/BallooFestival.jpg'
import Road from '../static/Valparai.cms'
import styled from 'styled-components'
import './myStyles.css'


var Butt=styled.button `
    color:green;
    padding:5px;
    
    font-weight:bold;
    background-color:#fff;
    border-radius:8px;
  
`

function CardTiles(props) {
    
    const passPlace=(event)=>{
        props.parentCallback(event.target.value);
        event.preventDefault();
        
    }
    
    return (
        <div className="row">
            <div className="col-lg-6 col-md-12">
                <Card>
                    <Card.Img variant="top" src={Road}/>
                    <Card.Body>
                        <Card.Title>Valparai</Card.Title>
                        <Card.Text>
                        
                        </Card.Text>
                        <Butt variant="primary" value="valparai" onClick={(event)=>passPlace(event)}>Explore Location</Butt>
                    </Card.Body>
                </Card>
            </div>
            <div className="col-lg-6 col-md-12">
                <Card >
                    <Card.Img variant="top" src={Aa} />
                    <Card.Body>
                        <Card.Title>Aazhiyar</Card.Title>
                        <Card.Text>
                        
                        </Card.Text>
                        <Butt variant="primary" value="Aazhiyar" onClick={(event)=>passPlace(event)}>Explore Location</Butt>
                    </Card.Body>
                </Card>
            </div>
            <div className="col-lg-6 col-md-12">
                <Card >
                    <Card.Img variant="top" src={Tops} />
                    <Card.Body>
                        <Card.Title> Topslip</Card.Title>
                        <Card.Text>
                       
                        </Card.Text>
                        <Butt variant="primary" value="Topslip" onClick={(event)=>passPlace(event)}>Explore Location</Butt>
                    </Card.Body>
                </Card>
            </div>
            <div className="col-lg-6 col-md-12">
                <Card >
                    <Card.Img variant="top" src={Bal} />
                    <Card.Body>
                        <Card.Title>BalloonFest</Card.Title>
                        <Card.Text>
                        
                        </Card.Text>
                        <Butt variant="primary" value="Balloon Festival" onClick={(event)=>passPlace(event)}>Explore Location</Butt>
                    </Card.Body>
                </Card>
            </div>
        </div>
    )
}

export default CardTiles
