import React,{useState} from 'react'
import './myStyles.css'
import {Carousel} from 'react-bootstrap'
import PollachiEco from '../static/pollachi-travel.jpg'
import Aazhiyar from '../static/AazhiyarTop.jpg'
import VTerrain from '../static/Valparaiterrain.jpg'
import CardTiles from './CardTiles'
import OutputWindow from './OutputWindow'
import Temp from './Temp'



function Pagebody() {
    const[place,setPlace]=useState("hi")

    const handleCallback=(data)=>{
        setPlace(data);
        var elem=document.getElementById("opWin");
        elem.scrollIntoView();

    }
    return (
        <div className="row ">
            <div className="col-sm-6">
                <Carousel className="container">
                    <Carousel.Item>
                        <img
                        className="d-block w-100"
                        src={VTerrain}
                        alt="First slide"
                        />
                        <Carousel.Caption>
                        {/* <h3>First slide label</h3> */}
                    
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                        className="d-block w-100"
                        src={PollachiEco}
                        alt="Second slide"
                        />

                        <Carousel.Caption>
                        {/* <h3>Second slide label</h3> */}
                        
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                        className="d-block w-100"
                        src={Aazhiyar}
                        alt="Third slide"
                        />

                        <Carousel.Caption>
                        {/* <h3>Third slide label</h3> */}
                        
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
                <div id="opWin">
                <OutputWindow name={place}/>
                </div>
                
                
            </div>
                <div className="col-sm-6" id="tiles">
                    <CardTiles  parentCallback={handleCallback}/>
                    <iframe id="paradise" src="https://www.youtube.com/embed/UCEWM2Arkfg" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </div>
                <div>
                    
                </div>
        </div>
        
    )
}

export default Pagebody
