import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import {Jumbotron,Container,Button} from 'react-bootstrap'
import Img from '../static/PollachiEco.jpg'
import './myStyles.css'
import styled from 'styled-components'

const H1=styled.h1`
    color:#fff;
    text-shadow:4px 2px black;
    opacity:0.9;
`
const P=styled.p`
font-size:17px;
color:white;
font-weight:bold;
text-shadow:4px 2px black;
opacity:0.9;
`

function jumbo() {
    return (
        <div className="container-fluid"  >
            <Jumbotron fluid id="jumb" >
                <Container >
                    <H1>POLLACHI TOURISM</H1>
                    <P>
                   Welcome to the Coconut city!
                    </P>
                </Container>
            </Jumbotron>
        </div>
    )
}
// style={{backgroundImage:`url(${Img})`, height:`600px`}}

export default jumbo
