import React from 'react'
import Aazhiyar from './Aazhiyar'
import BalloonFestival from './BalloonFestival'
import TopSlip from './TopSlip'
import Vaalparai from './Vaalparai'
import styled from 'styled-components'

const H1=styled.h1`
color:#fff;
`
const Poy=styled.div`
color:#fff;
padding:10px;
font-size:17px;
text-align:justify;
border:1px solid white;
opacity:0.8;
margin:5px;
`
const P=styled.span`
font-weight:bold;
font-size:19 px;
`

function OutputWindow(props) {
    if (props.name=='Topslip'){
        return(
            <div>
                <TopSlip/>
            </div>
        )
    }
    if (props.name=='Aazhiyar'){
        return(
            <div>
                <Aazhiyar/>
            </div>
        )
    }
    if (props.name=='Balloon Festival'){
        return(
            <div>
                <BalloonFestival/>
            </div>
        )
    }
    if (props.name=='valparai'){
        return(
            <div>
                <Vaalparai/>
            </div>
        )
    }
    return(<div id="def">
       <H1>POLLACHI</H1> 
       <Poy>
        <P>Pollachi</P> is a town and a taluk headquarters in Coimbatore district, 
        Tamil Nadu state, India. Located about 40 km to the south of Coimbatore, 
        it is the second largest town in the district after Coimbatore. 
        Pollachi was known as Pozhil Vaitchi in Tamil which means "gifted with beauty"
         and later became Pollachi. It was also known as Mudi Konda Chola Nallur during the period of the Cholas.
         The area is hilly and rocky, drained by several rivers and iss thickly forested with some marsh lands and scattered patches of grass. 
          <P> Parambikulam-Aliya</P>r multipurpose project involves a series of dams interconnected by tunnels and canals at various elevations to harness the various rivers flowing through the area.
          Azhiyar Dam is located on the foothills of <P>Anamalai</P> in the Western Ghats and Solaiyar Dam is 15 kilometres (9.3 mi) from Valparai and is one of the largest rock dams in India.
         <P> Valparai</P> is a hill station about 65 kilometres (40 mi) 
         from Pollachi and is situated at an altitude of 3,500 feet (1,100 m). <P>Topslip</P> is a popular picnic spot located at an altitude of about 800 feet (240 m) in 
         the Anamalai mountain range which is about 37 kilometres (23 mi) from Pollachi. <P>Aanaimalai Tiger Reserve</P>, earlier known as Indira Gandhi Wildlife Sanctuary and National Park and previously as Aanaimalai Wildlife Sanctuary,
            is a protected area located in the Anaimalai Hills of Pollachi 
          Parambikulam Wildlife Sanctuary is situated in the valley between the Anaimalai Hills of Tamil Nadu and the Nelliampathi range of Kerala.
           The economy of the town is predominantly dependent on agriculture. Coconut, jaggery, vegetables and cattle contribute to the agricultural output.
            There are also coir producing units with raw materials sourced from local farms. Vanilla is also cultivated in certain locations.
           Pollachi is also a popular movie shooting location for the Tamil film industry owing to its scenic landscape.
           
       </Poy>
    </div>)
    
}

export default OutputWindow
